import React, { Component } from 'react';
import axios from 'axios';
import './App.css';
import Table from "react-bootstrap/lib/Table";
import Image from "react-bootstrap/lib/Image";
import 'font-awesome/css/font-awesome.css'


class App extends Component {
    
  state = {
    top30Days: [],
    topAllTime: [],
    current: true
  }  

  getFCCData(url, stateName) {
    axios.get(url)
    .then(({data}) => {
      this.setState({ [stateName]: data});
    })
  }

  pointChange(value) {
    if (this.state.current !== value) {
      this.setState({current : value})
    }
  }

  componentDidMount() {
    this.getFCCData('https://fcctop100.herokuapp.com/api/fccusers/top/recent', 'top30Days')
    this.getFCCData('https://fcctop100.herokuapp.com/api/fccusers/top/alltime', 'topAllTime')
  }

  render() {

    const {top30Days, topAllTime, current} = this.state;

    return (
      <div className="App">
        <header className="App-header">
        FreeCodeCamp leaderboards
        </header>

        <div id="copyright">coded by Rafael Pessoa</div>

        <div className="container" id="main">  
         <div className="row">
          <div className="col-md-12" id="table">

            <Table striped bordered condensed hover>
              
              <thead id="tableHead">
                <tr>
                  <th>#</th>
                  <th>Name</th>
                  <th className="headers" onClick={(event) => this.pointChange(true)}>Points in 30 days &nbsp; <i className="fa fa-caret-down"></i></th>
                  <th className="headers" onClick={(event) => this.pointChange(false)}>All time points &nbsp; <i className="fa fa-caret-down"></i></th>
                </tr>
              </thead>
              
              <tbody>

              {current === true && top30Days.map((row, index) => (
                <tr>
                  <td>{index + 1}</td>
                  <td id="username"><a href={`http://freecodecamp.org/${row.username}`}>
                   <Image src = {row.img} id="imgHeight"/> &nbsp; {row.username}
                  </a></td>
                  <td>{row.recent}</td>
                  <td>{row.alltime}</td>
                </tr>
                )
              )}

              {current === false && topAllTime.map((row, index) => (
                <tr>
                  <td>{index + 1}</td>
                  <td id="username"><a href={`http://freecodecamp.org/${row.username}`}>
                   <Image src = {row.img} id="imgHeight"/> &nbsp; {row.username}
                  </a></td>
                  <td>{row.recent}</td>
                  <td>{row.alltime}</td>
                </tr>
                )
              )}

              </tbody>

            </Table>       

          </div>
         </div>
        </div>

      </div>
    );
  }
}

export default App;



